import styles from './index.module.css';
import Separator from '../separator';

const Section = ({ title, children, expandable, expanded, expand }) => {
    console.log(expand)

    return (
        <div className={
            [
                expandable ? styles.Expandable : null,
                expandable && expanded ? styles.Expanded : null
            ].join(" ")
        }>
            <Separator animateOnSight />
            <h2>{ title }</h2>
            <div>
                { children }
            </div>
            { expandable && !expanded ? <i onClick={ e => expand() } className={["fas fa-sort-down", styles.Expand].join(" ")}></i> : null }
        </div>
    )
}

export default Section;