import react, { useState, useEffect } from 'react';
import styles from './index.module.css';
import { inViewport, addInViewportListener } from '../../utils/in-viewport';

const Article = ({ title, date, children, animateOnSight }) => {
    const [visible, setVisible] = useState(false);
    
    const wrapper = react.createRef();

    const initScrollListener = () => {
        if (animateOnSight) {
            addInViewportListener(wrapper, 0, v => {
                if (v === visible) return;
                setVisible(true);
            });
        }
    }

    useEffect(() => {
        initScrollListener();
        if (inViewport(wrapper, 0)) setVisible(true);
    });

    return (
        <div ref={ wrapper }>
            <h3 className={ 
                animateOnSight && visible ? styles.Animation : null
            }>{ title }</h3>
            <p  className={
                [
                    styles.Date,
                    animateOnSight && visible ? styles.Animation : null
                ].join(" ")
            }>{ date }</p>
            <div className={ 
                [
                    styles.Children,
                    animateOnSight && visible ? styles.Animation : null
                ].join(" ")
             }>
                { children }
            </div>
        </div>
    )
}

export default Article;