const inViewport = (element, offset) => {
    if (!element || !element.current) return;
    const bounds = element.current.getBoundingClientRect();
    const windowBottom = window.innerHeight;
        
    return (bounds.top + offset >= 0 && bounds.top - offset <= windowBottom);
}

const addInViewportListener = (element, offset, cb) => {
    window.addEventListener("scroll", () => {
        if (!element || !element.current) return;
        const bounds = element.current.getBoundingClientRect();
        const windowBottom = window.innerHeight;
        
        cb(bounds.top + offset >= 0 && bounds.top - offset <= windowBottom);
    });
}

export { inViewport, addInViewportListener };