import { useState } from 'react';
import styles from './index.module.css';
import Introduction from '../../sections/introduction';
import EmploymentHistory from '../../sections/employment-history';
import Education from '../../sections/education';
import DownloadCV from '../../sections/download-cv';
import Projects from '../../sections/projects';

const Content = () => {
    const [educationExpanded, setEducationExpanded] = useState(false);
    const [projectsExpanded, setProjectsExpanded] = useState(false);

    const expandEducation = e => {
        setEducationExpanded(true);
    }

    const expandProjects = e => {
        setProjectsExpanded(true);
    }

    return (
        <main className={ styles.Content }>
            <Introduction />
            <EmploymentHistory />
            <Education expanded={ educationExpanded } expand={ () => setEducationExpanded(true) } />
            <Projects expanded={ projectsExpanded } expand={ () => setProjectsExpanded(true) } />
            <DownloadCV />
        </main>
    )
}

export default Content;