import react, { useState, useEffect } from 'react';
import styles from './index.module.css';
import { inViewport, addInViewportListener } from '../../utils/in-viewport';

const Separator = ({ animate, animateOnSight, centered }) => {
    const [visible, setVisible] = useState(false);

    const wrapper = react.createRef();

    const initScrollListener = () => {
        if (animateOnSight) {
            addInViewportListener(wrapper, 0, v => {
                if (v === visible) return;
                setVisible(true);
            });
        }
    }

    useEffect(() => {
        initScrollListener();
        if (inViewport(wrapper, 0)) setVisible(true);
    });

    return (
        <div ref={ wrapper } className={[
            styles.Separator,
            animate ? styles.Animation : null,
            centered ? styles.Centered : null,
            animateOnSight && visible ? styles.Animation : null
            ].join(" ")}>
            <div style={{animationDelay: "0s"}}></div>
            <div style={{animationDelay: ".25s"}}></div>
            <div style={{animationDelay: ".5s"}}></div>
            <div style={{animationDelay: ".8s"}}></div>
        </div>
    )
}

export default Separator;